.App,
body {
  text-align: center;
}

a {
  text-decoration: none;
  color: #adadad;
  transition: 1s;
}

.links {
  color: #fff;
  transition: 1s;
  font-weight: 600;
  font-size: 2rem;
}

a:hover,
.links:hover {
  color: #fb4b74;
}

.dropdown-menu li {
  margin-bottom: 0.2rem;
}

.dropdown:hover .dropdown-menu {
  display: block;
}
.dropdown-menu {
  margin-top: 0;
}

.subLink:hover,
.active-subLink {
  color: #fff !important;
  border-radius: 10px 0 10px 0 !important;
  background-color: #fb4b74 !important;
}

.subLink {
  margin: "0 .8rem .2rem" !important;
}

.h1-alternative {
  font-size: 4rem;
}

h1 {
  font-size: 5.5rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 1.5rem;
}

h4 {
  font-size: 1.2rem;
}

h5 {
  font-size: 1rem;
}

h6 {
  font-size: 0.9rem;
}

.contact-link {
  font-size: 0.9rem;
  color: #282c34;
  font-weight: 500;
  margin-bottom: 10px;
}

.link-box {
  border: 8px solid #fff;
  border-left: none;
}

#firstBox {
  border: 8px solid #fff;
}

.img-link {
  position: relative;
  top: 0;
  left: 0;
}

.img-link h2 {
  color: #fb4b74;
  transition: 1s;
  margin: -45px 0 0;
  font-size: 0;
}

.img-link div {
  position: absolute;
  top: 0;
  left: 0;
  transition: 1s;
  bottom: 0;
  width: 100%;
  height: 100%;
}

.img-link img {
  transition: 1s;
}

.img-link:hover img {
  filter: brightness(0.5);
}

.img-link:hover h2 {
  font-size: 64px;
}

.logo-visible {
  opacity: 0;
  font-size: 0;
  animation: show-logo 0.5s ease-in-out forwards;
}

@keyframes show-logo {
  100% {
    font-size: 1rem;
    opacity: 1;
  }
}

.logo-hidden {
  font-size: 0;
  opacity: 0;
  animation: hide-logo 2s ease-in-out forwards;
}

@keyframes hide-logo {
  100% {
    opacity: 0;
    font-size: 0;
  }
}

.black-bg {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.basketContainer {
  position: fixed;
  right: 0;
  top: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  z-index: 100;
}

.basketIcon {
  color: #fff;
}

.basketIconWrapper {
  background-color: #fb4b74;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1;
  width: 60px;
  height: 50px;
  transition: 0.3s;
}

.basketWrapper {
  height: 50px;
  max-width: 0;
  transition: 0.7s;
  justify-content: center;
  position: relative;
  right: -100%;
  z-index: 0;
  background-color: #000;
  border-radius: 4px 0px 0px 4px;
}

.basketIconText {
  color: white;
  margin: 0 5px;
}

.bIconContainer {
  margin: 0 0 3px;
}

.bIconContainer:hover .basketIconWrapper {
  width: 70px;
}

.bIconContainer:hover .basketWrapper {
  max-width: 500px;
  right: 0;
}

.grid {
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

#icon1,
#icon2 {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  color: #fb4b74;
  transition: 1s;
  width: 45px;
  height: 45px;
  margin-right: 15px;
  background-color: #63636333;
}

.headerText {
  margin: 0;
}

#icon2 {
  margin-right: 0;
  margin-left: 15px;
}

#icon1:hover,
#icon2:hover {
  color: #fff;
  background-color: #fb4b74;
}

.divLine {
  border-left: 1px solid #999999;
}

.separator {
  margin: 30px auto;
  border-top: 2px solid #fb4b74;
  border-bottom: 2px solid #fb4b74;
  width: 20%;
  height: 4px;
}

.centerItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.nav-link {
  color: #4d4d4d;
  font-weight: 500;
  padding: 0.5em 0.8em;
  margin: 0 0.2rem;
  transition: 1s;
}

.nav-link:hover,
.active-link {
  color: #fb4b74;
  background-color: #63636333;
  border-radius: 10px 0 10px 0;
}

.nav-link:hover {
  color: #fb4b74;
  background-color: #63636333;
  border-radius: 0px 10px 0px 10px;
}

.flexDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.social-icons {
  color: #282c34;
  transition: 1s;
  margin: 5px;
}

.social-icons:hover {
  transform: rotate(45deg);
  color: #fb4b74;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#carouselAnimation {
  animation: zoom 8s ease-in-out forwards;
}

#carouselText1 {
  opacity: 0;
  animation: bottom-to-top 0.5s ease-in forwards 1s;
}

#carouselText2 {
  opacity: 0;
  animation: bottom-to-top 0.5s ease-in forwards 2s;
}

#carouselBtn {
  opacity: 0;
  animation: bottom-to-top 0.5s ease-in forwards 3s;
}

@keyframes bottom-to-top {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes zoom {
  100% {
    transform: scale(1.5);
  }
}

input[type="text"],
input[type="email"],
input[type="password"],
textarea {
  border: none;
  padding: 15px 10px;
  margin: 10px 0;
}

.checkbox {
  position: absolute;
  opacity: 0;
  top: 0;
  left: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}

/* The container */
label {
  display: block;
  position: relative;
  padding-left: 25px;
  margin-top: 4px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* Create a custom checkbox */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 15px;
  width: 15px;
  background-color: #eee;
  border: 1px solid #fb4b74;
}

/* On mouse-over, add a grey background color */
label:hover input ~ .checkmark {
  background-color: #ccc;
}

/* When the checkbox is checked, add a blue background */
label input:checked ~ .checkmark {
  background-color: #fb4b74;
}

/* Create the checkmark/indicator (hidden when not checked) */
label:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the checkmark when checked */
label input:checked ~ .checkmark:after {
  display: block;
}

@media (max-width: 768px) {
  #contact {
    align-items: center !important;
    text-align: center !important;
  }
}
